  ngOnInit(): void {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('userLogin');

    let today = new Date();
    let month = (today.getMonth() + 1);
    let day = today.getDate();
    let pemitIssueDate = (day > 9 ? day : ('0' + day)) + '-' + (month > 9 ? month : ('0' + month)) + '-' + today.getFullYear();

    this.userLoginDetails = this.common.getValueFromSessionStorage('userLoginInfo');

    this.reactiveFormName = this.formBuilder.group({
      emiratesId: ['', [Validators.required, Validators.minLength(15), Validators.maxLength(15)]],
      firstName: ['', [Validators.required]],
      lastName: [''],
      arabicName: ['', [Validators.required]],
      emiratesIdExpiryDate: [null, [Validators.required]],
      nationality: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.pattern(this.common.emailPattern)]],
      mobileNo: ['', [Validators.required, Validators.minLength(12), Validators.maxLength(12)]],
      licenseNumber: ['', [Validators.required, Validators.pattern(this.common.nameRegularExpression)]],
      emirate: ['', [Validators.required]],
      issueDate: [null, [Validators.required]],
      expiryDate: [null, [Validators.required]],
      chooseType: ['', [Validators.required]],
      typeOfPermit: ['', [Validators.required]],
      permitIssueDate: [pemitIssueDate, [Validators.required]],
      permitExpiryDate: [null, [Validators.required]],
      permitFees: ['', [Validators.required]],
      gender: [null, [Validators.required]],
      street: [''],
      address1: [''],
      address2: [''],
      city: [''],
      state: [''],
      zip: [''],
      country: [''],
      notes: [''],
      checkAllCondition: ['', [Validators.required]],
      file1: [null, [Validators.required]],
      file2: [null],
      file3: [null],
      file4: [null],
      file5: [null, [Validators.required]],
      file6: [null]
    });

    this.getAllData();
  }

  // convenience getter for easy access to form fields
  get f() { return this.reactiveFormName.controls; }

  // Used for call all APIs
  async getAllData() {
    let companyinfo = this.common.getValueFromSessionStorage('selectedCompanyInfo');
    if (companyinfo.CompanyType == "Franchise") {
      this.common.isCompanyTypeFranchise = true;
    }
    else {
      this.common.isCompanyTypeFranchise = false;
    }
    let updateDriverInfo = this.common.getValueFromSessionStorage('updateDriverInfo');
    this.driveInfo = updateDriverInfo;
    if (updateDriverInfo != null) {
      if (updateDriverInfo.requestFrom == 'unapprovedUpdate') {
        this.reactiveFormName.get('file1').clearValidators();
        this.reactiveFormName.get('file5').clearValidators();
        this.requestFlag = 2;
        this.showAttachment = false;
      } else if (updateDriverInfo.requestFrom == 'driverUpdateReqCreation') {
        this.requestFlag = 3;
      } else if (updateDriverInfo.requestFrom == 'driverUpdateReqUpdate') {
        this.requestFlag = 4;
      } else if (updateDriverInfo.requestFrom == 'editUpdate') {
        this.requestFlag = 5;
        debugger;
        if (this.common.isCompanyTypeFranchise) { 
          this.showAttachment = false;
        } else {
          this.reactiveFormName.get('file1').clearValidators();
          this.reactiveFormName.get('file5').clearValidators();
          this.showAttachment = true;
        }       
      }
    }
    this.reactiveFormName.get('file1').updateValueAndValidity();
    this.reactiveFormName.get('file5').updateValueAndValidity();
    await this.getEmiratesList();
    await this.getFilterNationalityList();
    await this.getDriverTypePermitList();
    await this.getDriverTypeList();

    if (updateDriverInfo != null) {
      await this.assignDataToform(updateDriverInfo);
      this.getViewVehicleAttachmentsList();
    }

    if (this.requestFlag && this.common.isCompanyTypeFranchise) {
      debugger;
      this.isEmiratesIdReadOnly = false;
      this.isFirstNameReadOnly = false;
      this.isLastNameReadOnly = false;
      this.isNameInArabicReadOnly = false;
      this.f.nationality.enable();
      this.f.emiratesIdExpiryDate.enable();
    }
    else if (this.requestFlag == 2 || (this.requestFlag == 5 && !this.common.isCompanyTypeFranchise)) {//added franchise company check to make editable below fields radhika 14-12-2023
      this.isEmiratesIdReadOnly = true;
      this.isFirstNameReadOnly = true;
      this.isLastNameReadOnly = true;
      this.isNameInArabicReadOnly = true;
      this.f.nationality.disable();
      this.f.emiratesIdExpiryDate.disable();
    } else {

    }
  }

  public ngOnDestroy(): void {
    this.common.deleteValueFromSessionStorage('updateDriverInfo');
  }

